<!-- src/views/ProductionMenu.vue -->
<template>
    <div class="produccion-menu-container">
      <h1 class="main-title">Módulo de Producción</h1>
      <h2 class="cliente-nombre" v-if="store.state.cliente.nombre">
        {{ store.state.cliente.nombre }}
      </h2>
      <div class="sections-grid">
        <!-- Produccion -->
        <div class="section produccion-section" v-if="store.getters.hasPermission('inventario', null, 'ver')">
          <h2 class="section-title">
            <font-awesome-icon icon="industry" class="card-icon" /> PRODUCCIÓN
          </h2>
          <div class="cards-container">
            <router-link
