              to="/production/admin"
              class="card"
            >
              <font-awesome-icon icon="trowel-bricks" class="card-icon" />
              <span>Gestión de Producción</span>
            </router-link>
            <router-link
              v-if="store.getters.hasPermission('production', 'reportes', 'ver')"
              to="/production/reportes"
              class="card"
            >
              <font-awesome-icon icon="chart-bar" class="card-icon" />
              <span>Reportes de Producción</span>
            </router-link>
          </div>
        </div>
      </div>

    </div>
  </template>


  <script>

  import { useStore } from 'vuex';
  import { FontAwesomeIcon } from '@fortawesome/vue-fontawesome';

  export default {
    name: 'ProductionMenu',
    components: { FontAwesomeIcon },
    setup() {
      const store = useStore();
      return { store };
    },
  };

  </script>
  
