  .produccion-menu-container {
    padding: 20px;
    max-width: 1400px;
    margin: 0 auto;
  }
  
  .main-title {
    font-size: 2rem;
    font-weight: 700;
    color: #2c3e50;
    text-align: center;
    margin-bottom: 20px;
  }
  
  .cliente-nombre {
    font-size: 1.5rem;
    color: #34495e;
    text-align: center;
    margin-bottom: 30px;
  }
  
  .sections-grid {
    display: flex;
    justify-content: center;
    gap: 30px;
    flex-wrap: wrap;
  }
  
  .section {
    background: #ffffff;
    border-radius: 12px;
    box-shadow: 0 4px 10px rgba(0, 0, 0, 0.1);
    padding: 20px;
